.popup__wrapper {
    display: flex;
    align-items: center;
}

@media (max-width: 1024px) {
    .popup__wrapper {
        align-items: flex-start;
    }
}

@media (max-width: 825px) {
    .popup__wrapper {
        padding-bottom: 24px;
    }
}

@media (max-width: 580px) {
    .popup__wrapper {
        flex-direction: column;
        align-items: center;

        padding-bottom: 20px;
    }
}