.popup__info-list {
    display: flex;
    flex-wrap: wrap;

    list-style: none;

    margin: 0;
    padding: 0;
    margin-bottom: 6px;

    max-width: 570px;
}

@media (max-width: 825px) {
    .popup__info-list {
        max-width: 350px;
    }
}

@media (max-width: 580px) {
    .popup__info-list {
        max-width: 100%;
    }
}