.popup__price {
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 36px;
    font-feature-settings: 'liga' off;
    color: #383736;

    margin: 0;
    padding: 0;
    margin-top: 2px;
    margin-bottom: 12px;
}

@media (max-width: 580px) {
    .popup__price {
        font-size: 20px;
        line-height: 28px;

        margin-top: 0;
    }
}