.popup__title {
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 36px;
    font-feature-settings: 'liga' off;
    color: #383736;

    margin: 0;
    padding: 0;
    margin-bottom: 8px;
}

@media (max-width: 580px) {
    .popup__title {
        font-size: 20px;
        line-height: 28px;

        margin-bottom: 16px;
    }
}