.popup {
    display: flex;
    align-items: center;
    justify-content: center;

    position: fixed;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;
    min-height: min-content;
    box-sizing: border-box;

    padding: 0 28px;

    font-family: Roboto, Arial, Helvetica, sans-serif;

    overflow: auto;
}

@media (max-width: 825px) {
    .popup {
        align-items: flex-start;
    }
}

@media (max-width: 580px) {
    .popup {
        padding: 0 8px;
    }
}