.popup__info-item {
    display: flex;
    flex-direction: column;

    margin-right: 44px;
    margin-bottom: 12px;

    width: 100px;
}

@media (max-width: 580px) {
    .popup__info-item {
        margin-right: 15px;
    }
}

@media (max-width: 580px) {
    .popup__info-item {
        width: calc(50% - 10px);

        margin-right: 20px;

        &:nth-child(2n) {
            margin-right: 0;
        }
    }
}