.popup__close {
    display: flex;
    align-items: center;
    justify-content: center;

    position: absolute;
    top: 16px;
    right: 16px;

    padding: 0;
    margin: 0;

    width: 24px;
    height: 24px;
    box-sizing: border-box;

    background-color: rgba(0, 0, 0, 0);
    fill: #B2B0AB;

    border: none;
    border-radius: 4px;

    cursor: pointer;

    &:hover {
        background-color: #F5F4F2;
        transition: all 0.3s;
        fill: #383736;
    }

    &:active {
        background: #E7E4DE;
        fill: #383736;
    }
}